html{

  font-size: 62.5%;
}



body {
  margin: 0;
  font-family: 300 11px/1,4 'Helvetica Neue', 'sans-serif';
  color: black;
  background-color: #0a87cc;
  overflow:  hidden;
  display: block;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.dashboard {
  margin-top: 200px;
  margin-left: 200px;
}

@media screen and (max-width: 1200px) {
  body {
    overflow: visible;
  }
}